import * as THREE from 'three';
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js';
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader.js';
import { DRACOLoader } from 'three/examples/jsm/loaders/DRACOLoader.js';
import GUI from 'lil-gui';
import gsap from 'gsap';
import MotionPathPlugin from 'gsap/MotionPathPlugin';
import Stats from 'stats.js';


// NOTE-231213      Environment 타입
const EnvironmentType = {
    NORMAL: 'Normal',
    DESERT: 'Desert'
};

// NOTE-231213      변수들 선언
let currentEnvironmentType = EnvironmentType.DESERT;
const sandAnimSpeed = 1;
const cameraSwitchDuration = 2;



// NOTE-231213      전역 사이즈
const sizes = {
    width: window.innerWidth,
    height: window.innerHeight
};



// NOTE-231213      Scene
const scene = new THREE.Scene();




let currentAnimationIndex = 0;
const cameraPoints = [
    new THREE.Vector3(3, 2, 3),
    new THREE.Vector3(-2, 1.5, 2),
    new THREE.Vector3(-1.5, 1.2, -1.5),
    new THREE.Vector3(-0.5, 0.5, 2),
    new THREE.Vector3(-2, 1, -0.5),
    new THREE.Vector3(1.5, 0.5, 1),
    new THREE.Vector3(3, 2, 3)
];
const cameraLookAtPoints = [
    new THREE.Vector3(0, 1, 0),
    new THREE.Vector3(0, 1.38, 1.52),
    new THREE.Vector3(-0.28, 1.24, -0.5),
    new THREE.Vector3(0, 0.67, 1),
    new THREE.Vector3(-0.25, 0.67, -0.8),
    new THREE.Vector3(0.16, 0.25, 0.5),
    new THREE.Vector3(0, 1, 0)
];

let currentLookAtPoint = cameraLookAtPoints[currentAnimationIndex];

// NOTE-231213      카메라
const camera = new THREE.PerspectiveCamera(75, sizes.width / sizes.height);
camera.position.set(cameraPoints[currentAnimationIndex].x, cameraPoints[currentAnimationIndex].y, cameraPoints[currentAnimationIndex].z);
camera.lookAt(currentLookAtPoint.x, currentLookAtPoint.y, currentLookAtPoint.z);
scene.add(camera);


// NOTE-231213      DOM
const canvas = document.querySelector('canvas.webgl');



// NOTE-231213      Debug GUI
const debugGui = new GUI({
    width: 300,
    title: 'Nice debug UI',
    closeFolder: false
});

const debugTweaks = debugGui.addFolder('Debug');
debugTweaks.close();
debugGui.close();


// NOTE-231214      Stats 모니터링
const stats = new Stats();
stats.showPanel(0);     // NOTE-231214      0:fps, 1:ms, 2:mb, 3+:custom
//document.body.appendChild(stats.dom);





// NOTE-231214      GSAP 디깅중
gsap.registerPlugin(MotionPathPlugin);

let cameraMoveTween = null;
let orbitTargetMoveTween = null;


function PlayCameraAnimation(motionIndex)
{
    // TODO-231215      아니 이것도 버그를 못잡아서 젠장
    if (currentAnimationIndex == 0 && motionIndex == 6)
        return;

    if (currentAnimationIndex == motionIndex)
        return;

    currentAnimationIndex = motionIndex;

    // TODO-231214      도저히 모르겠는.. Vector3 배열의 값이 z만 빼놓고 0으로 변경되는 말도 안되는 버그
    //currentLookAtPoint = cameraLookAtPoints[motionIndex];
    if (cameraMoveTween != null)
        cameraMoveTween.kill();

    if (orbitTargetMoveTween != null)
        orbitTargetMoveTween.kill();

    cameraMoveTween = gsap.to(camera.position, {
        duration: cameraSwitchDuration,
        motionPath: GetMotionPath(motionIndex),
        ease: 'power1.out',
    });
    orbitTargetMoveTween = gsap.to(currentLookAtPoint, {
        duration: cameraSwitchDuration,
        motionPath: GetLookAtMotionPath(motionIndex),
        ease: 'power1.out'
    });
}

function GetMotionPath(index)
{
    return {
        path: [{
            x: cameraPoints[index].x,
            y: cameraPoints[index].y,
            z: cameraPoints[index].z
        }]
    };
}

function GetLookAtMotionPath(index)
{
    return {
        path: [{
            x: cameraLookAtPoints[index].x,
            y: cameraLookAtPoints[index].y,
            z: cameraLookAtPoints[index].z
        }]
    };
}

window.addEventListener('keydown', (event) =>
{
    PlayCameraAnimationWrapper(event.key);
});

// TODO-231215      아니 버그를 못잡아서 이걸 만들고 있네
function PlayCameraAnimationWrapper(value)
{
    let usefulValue = false;
    if (value == '0' || value == 'Escape')
    {
        PlayCameraAnimation(6);
        usefulValue = true;
    }
    else
    {
        const integer = parseInt(value);
        if (isNaN(integer) == false
            && integer >= 1
            && integer <= 5)
        {
            PlayCameraAnimation(integer);
            usefulValue = true;
        }
    }

    if (usefulValue)
    {
        // NOTE-231215      작은 주황 원들 FadeInOut
        for (let i = 0; i < viewCircleButtons.length; ++i)
        {
            const btn = viewCircleButtons[i];
            if (value == 0 || value == 'Escape')
                btn.classList.remove('hide');
            else if (value == btn.getAttribute('data-index'))
                btn.classList.remove('hide');
            else
                btn.classList.add('hide');
        }
    }
}

window.addEventListener('mousedown', (event) =>
{
    if (event.target.className == 'viewButton')
        return;

    if (cameraMoveTween != null)
        cameraMoveTween.timeScale(5);

    if (orbitTargetMoveTween != null)
        orbitTargetMoveTween.timeScale(5);
});

window.addEventListener('mouseup', (event) =>
{
    if (event.target.className == 'viewButton')
        return;

    if (cameraMoveTween != null)
        cameraMoveTween.timeScale(1);

    if (orbitTargetMoveTween != null)
        orbitTargetMoveTween.timeScale(1);
});








// NOTE-231213      사막여우
const dracoLoader = new DRACOLoader();
dracoLoader.setDecoderPath('/draco/');

const gltfLoader = new GLTFLoader();
gltfLoader.setDRACOLoader(dracoLoader);

let mixer = null;
let gltf = null;
gltfLoader.load(
    './models/Fox/glTF/Fox.gltf',
    (loadedGltf) =>
    {
        gltf = loadedGltf;
        var model = gltf.scene;
        model.scale.set(0.025, 0.025, 0.025);

        // 각 메쉬에 그림자 설정 추가
        model.traverse((object) =>
        {
            if (object.isMesh)
                object.castShadow = true;
        });

        // Animation
        mixer = new THREE.AnimationMixer(model);
        PlayAnimation(2);
        scene.add(model);
    }
);

function PlayAnimation(index)
{
    if (mixer)
        mixer.stopAllAction();

    const action = mixer.clipAction(gltf.animations[index]);
    action.play();
}



// NOTE-231213      바닥 텍스쳐
const loadingManager = new THREE.LoadingManager();
const textureLoader = new THREE.TextureLoader(loadingManager);

const sandTexture = textureLoader.load('./textures/sandTexture.jpg');
sandTexture.colorSpace = THREE.SRGBColorSpace;
sandTexture.wrapS = THREE.MirroredRepeatWrapping;
sandTexture.wrapT = THREE.MirroredRepeatWrapping;
sandTexture.generateMipmaps = true;
sandTexture.minFilter = THREE.LinearMipmapNearestFilter;
sandTexture.magFilter = THREE.NearestFilter;
sandTexture.repeat.set(200, 200);

const snowTexture = textureLoader.load('./textures/snowTexture.jpg');
snowTexture.colorSpace = THREE.SRGBColorSpace;
snowTexture.wrapS = THREE.MirroredRepeatWrapping;
snowTexture.wrapT = THREE.MirroredRepeatWrapping;
snowTexture.generateMipmaps = true;
snowTexture.minFilter = THREE.LinearMipmapNearestFilter;
snowTexture.magFilter = THREE.NearestFilter;
snowTexture.repeat.set(150, 150);


// NOTE-231213      환경 이미지
const desertMap = textureLoader.load('./textures/desert_skybox.jpg');
desertMap.mapping = THREE.EquirectangularReflectionMapping;
desertMap.colorSpace = THREE.SRGBColorSpace;

const whiteMap = textureLoader.load('./textures/white_skybox.jpg');
whiteMap.mapping = THREE.EquirectangularReflectionMapping;
whiteMap.colorSpace = THREE.SRGBColorSpace;

SwitchMap(currentEnvironmentType);

function SwitchMap(type)
{
    var targetMap = type == EnvironmentType.DESERT ? desertMap : whiteMap;
    scene.background = targetMap;
    scene.environment = targetMap;
}





// NOTE-231213      바닥 매쉬
const sandMaterial = new THREE.MeshStandardMaterial({ map: sandTexture });
const snowMaterial = new THREE.MeshStandardMaterial({ map: snowTexture });

const floorGeometry = new THREE.PlaneGeometry(1000, 1000);

const floorMesh = new THREE.Mesh(floorGeometry, sandMaterial);
floorMesh.rotation.x = Math.PI * -0.5;
floorMesh.receiveShadow = true;
scene.add(floorMesh);

function SwitchFloorMaterial(type)
{
    floorMesh.material = type == EnvironmentType.DESERT ? sandMaterial : snowMaterial;
}






// NOTE-231213      빛
const ambientLight = new THREE.AmbientLight(0xffffff, 2.4);
scene.add(ambientLight);

const directionalLight = new THREE.DirectionalLight(0xffffff, 1.8);
directionalLight.castShadow = true;
directionalLight.shadow.mapSize.set(1024, 1024);
directionalLight.shadow.camera.far = 30;
directionalLight.shadow.camera.left = -10;
directionalLight.shadow.camera.top = 10;
directionalLight.shadow.camera.right = 10;
directionalLight.shadow.camera.bottom = -10;
directionalLight.position.set(-10, 10, 0);
scene.add(directionalLight);




// NOTE-231213      안개
const desertColor = new THREE.Fog('#D2691E', 10, 200);
const snowColor = new THREE.Fog('#44C6E9', 10, 200);
scene.fog = desertColor;

function SwitchFog(type)
{
    scene.fog = type == EnvironmentType.DESERT ? desertColor : snowColor;
}







// NOTE-231213      윈도우 크기 조절시
window.addEventListener('resize', () =>
{
    // Update sizes
    sizes.width = window.innerWidth;
    sizes.height = window.innerHeight;

    // Update camera
    camera.aspect = sizes.width / sizes.height;
    camera.updateProjectionMatrix();

    // Update renderer
    renderer.setSize(sizes.width, sizes.height);
    renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2));
});




// NOTE-231213      마우스 커서
const cursor = {
    x: 0,
    y: 0
};
window.addEventListener('mousemove', (event) =>
{
    cursor.x = event.clientX / sizes.width - 0.5;
    cursor.y = -1 * (event.clientY / sizes.height - 0.5);
});










// NOTE-231213      OrbitControls
const controls = new OrbitControls(camera, canvas);
controls.enableDamping = true;
controls.enablePan = false;
controls.rotateSpeed = 0.3;
controls.dampingFactor = 0.1;
controls.minPolarAngle = 0.5;
controls.maxPolarAngle = 1.4;
controls.minDistance = 2;
controls.maxDistance = 15;
controls.target = currentLookAtPoint;



// NOTE-231213      랜더러
const renderer = new THREE.WebGLRenderer({
    canvas: canvas,
    antialias: true
});
renderer.setSize(sizes.width, sizes.height);
renderer.shadowMap.enabled = true;



// NOTE-231213      키보드 인풋
window.addEventListener('keydown', (event) =>
{
    if (event.key == 'a')
        SwitchEnvironment();
});

function SwitchEnvironment()
{
    currentEnvironmentType = currentEnvironmentType == EnvironmentType.NORMAL
        ? EnvironmentType.DESERT : EnvironmentType.NORMAL;

    var IsNormal = currentEnvironmentType == EnvironmentType.NORMAL;
    currentSandAnimSpeed = IsNormal ? 0 : sandAnimSpeed;

    SwitchMap(currentEnvironmentType);
    PlayAnimation(IsNormal ? 0 : 2);
    SwitchFloorMaterial(currentEnvironmentType);
    SwitchFog(currentEnvironmentType);
}



// NOTE-231215      작은 주황색 원 버튼들
var viewCircleButtons = document.getElementsByClassName('viewCircleButton');
for (let i = 0; i < viewCircleButtons.length; ++i)
{
    viewCircleButtons[i].addEventListener('click', () =>
    {
        const index = viewCircleButtons[i].getAttribute('data-index');
        ChangeView(index);
    });
}



// NOTE-231213      업데이트문
const clock = new THREE.Clock();
let previousTime = 0;

let sandAnimValue = 0;
let currentSandAnimSpeed = sandAnimSpeed;

const animate = () =>
{
    stats.begin();

    const elapsedTime = clock.getElapsedTime();
    const deltaTime = elapsedTime - previousTime;
    previousTime = elapsedTime;

    // NOTE-231213      애니메이션 돌리는 부분
    if (mixer)
        mixer.update(deltaTime);

    // NOTE-231213      OrbitControls 하는 부분
    //mesh.rotation.y = elapsedTime;
    controls.update();

    sandAnimValue -= deltaTime * currentSandAnimSpeed;
    if (sandAnimValue < -100)
        sandAnimValue = 0;
    sandTexture.offset.y = sandAnimValue;

    // NOTE-231215      카메라 지정 포인트를 바라봐!
    camera.lookAt(currentLookAtPoint.x, currentLookAtPoint.y, currentLookAtPoint.z);

    // NOTE-231215      작은 주황색 원들 위치 업데이트
    UpdatePositionViewCircleButtons();
    UpdateViewCircleButtonsVisible();

    renderer.render(scene, camera);
    window.requestAnimationFrame(animate);

    stats.end();
};

animate();



function UpdatePositionViewCircleButtons()
{
    for (let i = 0; i < viewCircleButtons.length; ++i)
    {
        // NOTE-231215      0은 처음으로라서 +1 함
        const pos = WorldToScreenPosition(cameraLookAtPoints[i + 1], camera);
        viewCircleButtons[i].style.left = `${pos.x}px`;
        viewCircleButtons[i].style.top = `${pos.y}px`;
    }
}




function WorldToScreenPosition(position, camera)
{
    const vector = position.clone();

    vector.project(camera);

    const widthHalf = 0.5 * renderer.domElement.width;
    const heightHalf = 0.5 * renderer.domElement.height;

    vector.x = (vector.x * widthHalf) + widthHalf;
    vector.y = -(vector.y * heightHalf) + heightHalf;

    return {
        x: vector.x,
        y: vector.y,
    };
}

window.addEventListener('keydown', (event) =>
{
    if (event.key == 'g')
    {
        console.log(`${circleButtonPos.x}, ${circleButtonPos.y}`);
    }
});





// NOTE-231215      HTML 버튼
function ChangeView(index)
{
    PlayCameraAnimationWrapper(index);
}

const viewChangeButtons = document.getElementsByClassName('viewButton');
for (let i = 0; i < viewChangeButtons.length; ++i)
{
    viewChangeButtons[i].addEventListener('click', (event) =>
    {
        const index = viewChangeButtons[i].getAttribute('data-index');
        ChangeView(index);
    });
}

function UpdateViewCircleButtonsVisible()
{
    // NOTE-231215      이것도 알수 없는 버그때문에 코드가 지저분함
    if (currentAnimationIndex >= 1 && currentAnimationIndex <= 5)
        return;

    for (let i = 1; i < cameraLookAtPoints.length - 1; ++i)
    {
        const point = cameraLookAtPoints[i].clone();
        const dist = camera.position.distanceTo(point);

        if (dist > 4.55)
            viewCircleButtons[i - 1].classList.add('hide');
        else
            viewCircleButtons[i - 1].classList.remove('hide');
    }
}